.landing-background {
  min-height: 100vh;
  min-width: 100vw;
  height:100%;
  width: 100%;
  background-size: cover;
  background-position: center center;
  display:flex;
  justify-content: center;
  align-items: center;
}


.landing-background h1 {
  font-family: 'Coda', cursive;
  font-size: 4rem;
  text-align: center;
  color: #fff;
  text-shadow: 5px 5px 10px rgba(0,0,0,.8)
}



.app-header {
  background-size: cover;
  background-position: center center;
  min-height: 250px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.app-header:after {
  content: '';
  background-color: rgba(0,0,0,.4);
  position: absolute;
  top:0;
  right:0;
  bottom: 0;
  left: 0;
  z-index: 1;
}


.app-title {
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  z-index: 10;
}

.paper-card {
  background-color: #fff;
  border-radius: 6px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.3);
  padding: 60px 40px;
  margin-top: -60px;
  position: relative;
  z-index: 10;
}


.question {
  font-size: 2.5rem;
  line-height: 2;
}


.result-item-container {
  display: inline-block;
  width: 100px;
  text-align: center;
}

.footer-container { 
  position: absolute;
  bottom: 0;
  left:0;
  right: 0;
}
